body{min-width:320px;background:#F1F5F7;font-family:"sofia-pro";color:#1D2579;}
a,a:hover{text-decoration:none;}
ul,ol,dl{margin:0;}
h1,.h1,h2,.h2,h3,.h3,h4,.h4,h5,.h5,h6,.h6{margin-top:0;}
.badge{font-weight:normal;padding:5px 8px 7px;}
.text-theme{color:#1D2579;}
.text-theme2{color:#24E096;}
a,.transition,.btn,.btn:after,.form-control,.custom-radio .checkmark,.custom-radio .checkmark:after,.wrapper .sidebar,.templates-holder .image-holder .overlay,.em-tabs .nav-link{transition: all ease-in-out 0.3s;}
.table-responsive{position:relative;z-index:1;}
.form-control{height:42px;}
.form-control:focus {background-color: #fff;border-color: #1D2579;box-shadow: none !important;}
.btn{padding:7px 25px 9px;border:1px solid;border-radius:26px;overflow:hidden;background:#fff;font-size:16px;position:relative;}
.btn:after{content:"";position:absolute;width:0;top:0;left:0;bottom:0;background-color:transparent;}
.btn:hover,.btn:focus,.btn:active,.btn.acive,.btn:active:focus{box-shadow:0 0 10px rgba(0,0,0,0.3);outline:none;}
.btn:hover:after{width:100%;}
.btn span{position: relative;z-index:1;}
.btn-primary,.btn-primary:focus,.btn-primary:active{border-color:#24E096;color:#24E096;background:#fff;}
.btn-primary:hover{background: #fff;color:#fff;border-color:#24E096;}
.btn-primary:after{background-color:#24E096;}
.btn-secondary,.btn-secondary:focus,.btn-secondary:active{border-color:#1D2579;color:#1D2579;background:#fff;}
.btn-secondary:hover{background: #fff;color:#fff;border-color:#1D2579;}
.btn-secondary:after{background-color:#1D2579;}
.btn-success,.btn-success:hover,.btn-success:focus{border:none;border-color:transparent;background-color: transparent;}
.custom-radio {display: inline-block;position: relative;padding-left: 25px;margin-bottom: 12px;cursor: pointer;font-size:18px;line-height:26px;-webkit-user-select: none;-ms-user-select: none;user-select: none;}
.custom-radio input {position: absolute;opacity: 0;cursor: pointer;}
.custom-radio .checkmark {position: absolute;top: 5px;left: 0;height: 18px;width: 18px;border:2px solid #1D2579;border-radius: 50%;}
.custom-radio input:checked ~ .checkmark {border-color:#24E096;}
.custom-radio .checkmark:after {content: "";position: absolute;display: none;}
.custom-radio input:checked ~ .checkmark:after {display: block;}
.custom-radio .checkmark:after {top: 2px;left: 2px;width: 10px;height: 10px;border-radius: 50%;background: #24E096;}
textarea.form-control {height: auto; resize:none;}
.w-auto label{width: auto !important;}
.w-auto .input-holder { width: calc(100% - 380px);}
.custom-checkbox {display: inline-block;position: relative;padding-left: 30px;margin-bottom: 12px;cursor: pointer;-webkit-user-select: none;-ms-user-select: none;user-select: none;font-size:18px;line-height:26px;}
.custom-checkbox.no-text{width:20px;height:20px;padding-left:20px;}
.custom-checkbox input {position: absolute;opacity: 0;cursor: pointer;height: 0;width: 0;}
.custom-checkbox .checkmark {position: absolute;top: 3px;left: 0;height: 20px;width: 20px;border:1px solid #1D2579;border-radius: 4px;}
.custom-checkbox.no-text .checkmark{top:0;}
.custom-checkbox .checkmark:after {content: "";position: absolute;display: none;}
.custom-checkbox input:checked ~ .checkmark:after {display: block;}
.custom-checkbox .checkmark:after {left: 6px;top: 2px;width: 7px;height: 12px;border: solid #1D2579;;border-width: 0 2px 2px 0;-webkit-transform: rotate(45deg);transform: rotate(45deg);}
